'use strict';

/**
* Return a list of BLAS matrix triangles.
*
* @module @stdlib/blas-base-matrix-triangles
*
* @example
* import matrixTriangles from '@stdlib/blas-base-matrix-triangles';
*
* var list = matrixTriangles();
* // e.g., returns [ 'upper', 'lower' ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import enumeration from './enum.js';


// MAIN //

setReadOnly( main, 'enum', enumeration );


// EXPORTS //

export default main;
